import React, {useState} from 'react'
import Category from './Category.jsx'
import {v4 as uuid} from 'uuid'

import '../styles/Categories.css'

function Categories({filter}) {
  const categories = [{name: 'ü¶Ñ Dise√±o', selected: false},
    {name: 'üíª Programaci√≥n', selected: false},
    {name: 'üõçÔ∏è Marketing Digital', selected: false},
    {name: 'üìë Productividad', selected: false},
    {name: 'üí∞ Freelance', selected: false},
    {name: 'üåé Ingl√©s', selected: false},
    {name: 'üíº Trabajo IT', selected: false},
    {name: 'üõ†Ô∏è Herramientas', selected: false},
    {name: 'üìö Roadmap', selected: false},
    {name: 'üë©üèª‚Äç Marca personal', selected: false}
  ]

  const [selected, setSelected] = useState(categories)
 
  const isSelected = (item) => {
    // item.target.classList.toggle('selected')
    const name = item.target.outerText
    const newState = selected.map(category => {
      if(category.selected) {
        category.selected = !category.selected
      }
      if(category.name === name){
        category.selected = !category.selected
      }
      return category
    })
    setSelected([
      ...newState
    ])
    filter(name)
  }

  return (
    <div className="Categories">
      {selected.map(item => <Category key={uuid()} name={item.name} selected={item.selected} isSelected={isSelected}/>)}
    </div>
  )
}

export default Categories